* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

body {
  color: #e6e6e6;
  background-color: #0f1115;
  font-family: Poppins, sans-serif;
  line-height: 1.6;
}

.navbar {
  backdrop-filter: blur(8px);
  z-index: 1000;
  background: #14161bd9;
  border-bottom: 1px solid #1f2228;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 3rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.logo {
  color: #3a82f7;
  font-size: 1.5rem;
  font-weight: 700;
}

.nav-links {
  align-items: center;
  transition: all .3s;
  display: flex;
}

.nav-links a {
  color: #e6e6e6;
  margin: 0 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.nav-links a:hover {
  color: #3a82f7;
}

.menu-toggle {
  color: #e6e6e6;
  cursor: pointer;
  font-size: 1.8rem;
  display: none;
}

.hero {
  background: radial-gradient(circle at 70% 40%, #181b22 0%, #0f1115 70%);
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 9rem 2rem 6rem;
  display: flex;
}

.hero-content {
  text-align: left;
  flex-wrap: wrap;
  align-items: center;
  gap: 3rem;
  max-width: 1000px;
  display: flex;
}

.profile-img {
  object-fit: cover;
  border: 4px solid #3a82f7;
  border-radius: 50%;
  width: 260px;
  height: 260px;
  box-shadow: 0 0 20px #3a82f74d;
}

.hero-text h1 {
  font-size: 2.5rem;
  font-weight: 600;
}

.hero-text span {
  color: #3a82f7;
}

.hero-text h2 {
  color: #c7c7c7;
  margin-bottom: 1rem;
  font-size: 1.4rem;
}

.hero-text p {
  color: #c5c5c5;
  max-width: 400px;
  margin-bottom: 1.5rem;
}

.social-links {
  margin-bottom: 1.5rem;
}

.social-links a {
  color: #3a82f7;
  margin-right: .75rem;
  font-size: 1.5rem;
  transition: color .3s;
}

.social-links a:hover {
  color: #fff;
}

.btn {
  color: #fff;
  background-color: #3a82f7;
  border: 2px solid #0000;
  border-radius: 8px;
  padding: .75rem 1.5rem;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.btn:hover {
  color: #3a82f7;
  background-color: #0000;
  border-color: #3a82f7;
}

section h2 {
  text-align: center;
  color: #fff;
  margin-bottom: 1rem;
  font-size: 2rem;
}

section p {
  color: #c5c5c5;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 2rem;
}

.about {
  background-color: #14161b;
  padding: 6rem 2rem;
}

.about-grid {
  flex-wrap: wrap;
  justify-content: center;
  gap: 3rem;
  margin-top: 2rem;
  display: flex;
}

.about-grid h3 {
  color: #3a82f7;
  margin-bottom: 1rem;
}

.hobbies ul {
  list-style: none;
}

.hobbies li {
  color: #ddd;
  margin-bottom: .5rem;
}

.skill-tags span {
  color: #e6e6e6;
  background: #1e2128;
  border-radius: 5px;
  margin: .3rem;
  padding: .4rem .8rem;
  font-size: .9rem;
  transition: background .3s;
  display: inline-block;
}

.skill-tags span:hover {
  background: #3a82f7;
}

.projects {
  background-color: #0f1115;
  padding: 6rem 2rem;
}

.project-grid {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  max-width: 1000px;
  margin: 0 auto;
  display: grid;
}

.project-card {
  cursor: pointer;
  background: #14161b;
  border: 1px solid #1e2128;
  border-radius: 12px;
  padding: 1.5rem;
  transition: all .3s;
}

.project-card:hover {
  border-color: #3a82f7;
  transform: translateY(-5px);
}

.project-card h3 {
  color: #fff;
  margin-bottom: .5rem;
}

.project-card p {
  color: #b5b5b5;
}

.project-card.disabled {
  opacity: .5;
  cursor: not-allowed;
}

.contacts {
  background-color: #14161b;
  padding: 6rem 2rem;
}

.contact-grid {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.2rem;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
}

.contact-card {
  color: #e6e6e6;
  background: #1e2128;
  border-radius: 8px;
  align-items: center;
  gap: .5rem;
  padding: 1rem 1.5rem;
  text-decoration: none;
  transition: background .3s;
  display: flex;
}

.contact-card:hover {
  color: #fff;
  background: #3a82f7;
}

.go-up {
  color: #fff;
  cursor: pointer;
  background: #3a82f7;
  border: none;
  border-radius: 50%;
  padding: .75rem;
  font-size: 1.5rem;
  transition: transform .3s;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}

.go-up:hover {
  transform: translateY(-4px);
}

@media (width <= 768px) {
  .hero-content {
    text-align: center;
    flex-direction: column;
  }

  .profile-img {
    margin-bottom: 1rem;
  }

  .nav-links {
    background-color: #14161b;
    border-top: 1px solid #1e2128;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 1rem 0;
    display: none;
    position: absolute;
    top: 70px;
    right: 0;
  }

  .nav-links.active {
    display: flex;
  }

  .nav-links a {
    padding: 1rem 0;
    font-size: 1.1rem;
  }

  .menu-toggle {
    display: block;
  }

  .menu-toggle.active i {
    transition: transform .3s;
    transform: rotate(90deg);
  }
}
